.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    background: white;
    box-shadow: 0 .04rem .08rem 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}
.header>div{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .left,.header .right{
    width: .6rem;
}
.header .title{
    flex: 1 1;
    font-size: .32rem;
}
.commentList {
  background: white;
  padding: .2rem; }
  .commentList .commentItem:last-child:after {
    width: 0;
    height: 0; }
  .commentList .commentItem {
    width: 100%;
    height: auto;
    position: relative;
    background: white;
    color: gray;
    padding-bottom: .1rem; }
    .commentList .commentItem:after {
      content: '';
      width: 200%;
      height: 1px;
      position: absolute;
      bottom: 0;
      transform: scale(0.5);
      transform-origin: 0;
      background-color: gray; }
    .commentList .commentItem .userInfo {
      height: .8rem;
      line-height: .8rem;
      font-size: .24rem;
      display: flex;
      justify-content: center;
      align-items: center; }
      .commentList .commentItem .userInfo div:nth-child(1) {
        flex: 1 1;
        color: black; }
      .commentList .commentItem .userInfo div:nth-child(2) {
        width: auto; }
    .commentList .commentItem .userComment {
      line-height: .6rem;
      font-size: .3rem; }
    .commentList .commentItem .commentPicList {
      display: flex; }
      .commentList .commentItem .commentPicList img {
        display: block;
        width: 100px;
        height: 100px;
        box-sizing: content-box;
        margin-right: .1rem; }


